<template>
  <div class="expertCell">
      <div class="userAvatar">
        <img :src="getUserAvatar" />
        <img class="icon" src="../../../images/main/v_tag.png">
      </div>
      <div class="user">
        <span>{{itemData.trueName|getName}}<i v-for="item in getLabel">{{item}}</i></span>
        <p>{{itemData.signContent|getSignContent}}</p>
        <div>
          <span>{{itemData.huinums}}个回答</span>
          <span>，{{itemData.cangnums}}人关注</span>
        </div>
      </div>
      <span class="twBtn" @click.stop.prevent="gotoZJTW()" v-if="userID != itemData.id">向TA提问</span>
      <span class="btn" @click.stop.prevent="follow()" v-if="!getIsFollow">关注</span>
      <span class="btn selectd" @click.stop.prevent="follow()" v-if="getIsFollow">取消关注</span>
      <!-- <div class="images" v-if="itemData.imgs.length"><img :src="getImgs" /></div> -->
      <!-- <h3>{{itemData.q_title}}</h3>
      <p>{{itemData.q_content}}</p> -->
  </div>
</template>

<script>
import {getStore,imgBaseUrl,isInArray} from '../../../components/common/mUtils'
import { Toast,Indicator,MessageBox} from 'mint-ui'
export default {
  data(){
    return{
      imgBaseUrl,
      userID:'',
      userToken:'',
      data:[],
      isLogin:false,
    }
  },
  props:['itemData'],
  filters:{
    getSignContent(text){
      if (text == '') {
        return '这家伙什么都没有留下....'
      }else {
        return text
      }
    },
    getName(name){
      if (name == '') {
        return '郴房网专家'
      }else {
        return name
      }
    }
  },
  created(){
    this.userID = getStore('userID')
    this.userToken = getStore('userToken')
    // console.log(this.userID)
    if (this.userID) {
      this.isLogin = true
    }
  },
  computed:{
    getUserAvatar:function(){
      if(this.itemData){
        if (this.itemData.avatarPath == '') {
          return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
        }else {
          let indexOf = this.itemData.avatarPath.indexOf('http://tu.07358.com')
          if(indexOf != -1){
            return this.itemData.avatarPath
          }else {
            if (this.itemData.avatarPath == '          ') {
              return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
            }else {
              let indexOf = this.itemData.avatarPath.indexOf('/users')
              if (indexOf) {
                return '/p.07358.com' + this.itemData.avatarPath
              }else {
                return imgBaseUrl + this.itemData.avatarPath
              }
            }
          }
        }
      }else {
        return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
      }
    },
    getLabel(){
      if (this.itemData.tags) {
        let imags = this.itemData.tags.split(',')
        return imags;
      }else {
        return []
      }
    },
    getIsFollow(){
      if (this.isLogin) {
        return this.itemData.cangzj.length > 0 ? true : false;
      }else {
        return false
      }
    }
  },
  mounted(){
  },
  components:{
  },
  methods: {
    gotoZJTW(){
      if (this.isLogin) {
        this.$router.push({path: '/publishAsk_pc', query:{zjid:this.itemData.id}})
      }else {
        this.$router.push('/login_pc')
      }
    },
    follow(){
      if (!this.validateLogin()) {
        return
      }
      if (this.userID == this.itemData.id) {
        Toast('不能关注自己');
        return
      }
      Indicator.open('提交中...');
      let param = {
        'itemid':this.itemData.id,
      }
      // let getUrl = this.CFWConfig.APIHost + '/v3/ask/dian?token='+this.userToken
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/gzzj?token='+this.userToken
      this.axios.post(getUrl,this.objToformData(param))
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          this.$emit('loadData')
        }
        Toast(response.data.message);
        Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    validateLogin(){
      if (!this.isLogin) {
        MessageBox.confirm('请先登录,确定登录吗？').then(({ action }) => {
          this.$router.push('/login_pc')
        }, response => {
        });
        return false
      }else {
        return true
      }
    },
    // 参数转化成表单对象
    objToformData(obj){
      // let params = new URLSearchParams()
      let params = new FormData()
      Object.keys(obj).forEach( (key, i) => {
        params.append(key, obj[key]);
      });
      return params;
    },
  }

}

</script>

<style scoped>
/***** 问题列表 *****/
.expertCell{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 20px 10px;
  border-bottom: 1px solid #e8e8e8;
  /*border-top: 1px solid #eee;*/
  background-color: #fff;
}
/*.ask_list li{border-bottom: 1px solid #eee;}*/
.expertCell .images{}
.expertCell  img{
  width: 60px;
  height: 60px;
  margin-right: 20px;
  border-radius: 30px;
  border: 1px solid #eee;
}

.expertCell .user{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-grow: 1;
}

.expertCell .user span{
  color: #000;
  font-weight: 700;
  text-align: left;
  margin-bottom: 3px;
  line-height: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.expertCell .user div{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.expertCell .user div span{
  font-weight: normal;
  font-size: 12px;
  color: #999;
}


.expertCell .user span i{
  font-style: normal;
  /*margin-left: 0px;*/
  padding: 2px 10px;
  color: #849aae;
  background-color:#f4f7f9;
  margin-right: 10px;
  font-size: 12px;
  margin-left: 10px;
  border-radius: 12px;
}

.expertCell .user p{
  margin-bottom: 20px;
}

.expertCell h3{
  font-size: 16px;
  color: #000;
  font-weight: 400;
  height: 36px;
  line-height: 36px;
  text-align: left;
  /*margin-top: 10px;*/
  font-weight: bold;
  overflow:hidden;
  text-overflow:ellipsis;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:1;
}

.expertCell p{
  text-align: left;
  font-size: 14px;
  line-height: 20px;
  /*background-color: #f7f7f7;*/
  padding:  3px 0px 0px;
  overflow:hidden;
  text-overflow:ellipsis;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:3;
  color: rgb(50,50,50);
}

.expertCell .btn{
  border: 1px solid #31a4fc;
  background-color: #31a4fc;
  font-size: 13px;
  padding: 5px 15px;
  border-radius: 2px;
  color: #fff;
  font-weight: bold;
  min-width: 60px;
}

.expertCell .twBtn{
  margin-right: 20px;
  border: 1px solid #fdb738;
  background-color: #fdb738;
  font-size: 13px;
  padding: 5px 15px;
  border-radius: 2px;
  color: #fff;
  font-weight: bold;
  min-width: 60px;
}

.expertCell .selectd{
  border: 1px solid #ddd;
  background-color: #ddd;
}

.expertCell .userAvatar{
  position: relative;
}

.expertCell .userAvatar .icon{
  position: absolute;
  bottom: 2px;
  right: -2px;
  width: 20px;
  height: 20px;
}


</style>



