<template>
  <div id="category_pc">
    <PCHeader></PCHeader>
    <!-- <template>
      <mt-header title="全民经纪人">
        <mt-button slot="right" @click = "gotoLogin()">登录</mt-button>
        </mt-header>search_not
    </template> -->

    <section class="nav">
      <PCCommonNav></PCCommonNav>
    </section>

    <section class="main_center">
      <div class="left">
        <section class="expert" v-if="expert">
          <div class="userAvatar">
            <img :src="getUserAvatar">
            <img class="icon" src="../../images/main/v_tag.png">
          </div>
          <div class="info">
            <span>{{expert.trueName}}</span>
            <p>{{expert.signContent}}</p>
            <div class="label"><i v-for="item in getLabel">{{item}}</i></div>
          </div>
          <span class="twBtn" @click.stop.prevent="gotoZJTW()" v-if="userID != expert.id">向TA提问</span>
          <i class="btn" v-if="!getIsFollow" @click="follow">关注</i>
          <i class="btn selectd" v-if="getIsFollow" @click="follow">取消关注</i>
        </section>

        <section class="expertlist">
          <div class="PCheader_title"><h2 style="margin-left:10px;">TA的回答({{askArr.length}})</h2></div>
          <li v-for="item in askArr" >
          <router-link :to="{path: '/askDetail_pc', query:{qid: item.q_id}}">
            <MyAnswerList  :itemData="item" :type="2"></MyAnswerList>
          </router-link>
          </li>
        </section>

      </div>
      <div class="right">
        <!-- <section class="user" v-if="userInfo">
          <div class="info"><img :src="getUserAvatar"><span>{{userInfo.trueName}}</span></div>
          <div class="my">
            <span class="RB"><i>我的提问</i><em>0</em></span>
            <span><i>我的回答</i><em>0</em></span>
          </div>
        </section> -->

        <section class="newask">
          <PCNewAskList></PCNewAskList>
        </section>
      </div>
    </section>

    <PCFooter></PCFooter>

  </div>
</template>

<script>
import { Toast,Indicator,MessageBox} from 'mint-ui'
import {getStore,imgBaseUrl} from '../../components/common/mUtils'
import PCHeader from '../../components/header/PCHeader'
import PCFooter from '../../components/footer/PCFooter'
import PCAskItem from '../../page/home/children/PCAskList'
import MyAnswerList from './pc/PCMyAnswerList'
import PCExpertCell from '../home/pc/PCExpertCell'
import PCNewAskList from './pc/PCNewAskList'
import PCCommonNav from '../../components/common/PCCommonNav'
import $ from 'webpack-zepto'
import httpTool from '../../utils/httpTool'
export default {
  data(){
    return{
      imgBaseUrl,
      userID:'',
      userToken:'',
      userInfo:null,
      isLogin:false,
      expert:null,
      askArr:[],
      searchStr:'',
      complete: false, // 数据是否加载完成
      loading: false, // 是否在请求中
      pageSize:10, // 默认显示10数据
      newAsk:[],
      eid:0,
    }
  },
  created(){
    this.userToken = getStore('userToken')
    this.userID = getStore("userID")
    this.eid = this.$route.query.eid;
    if (this.userID) {
      this.isLogin = true
    }

    // 检测是不是移动端
    this.isMobile()
  },
  filters:{
  },
  mounted(){
    // 获取专家资料
    this.getExpert()

    // 获取问题列表
    this.getAskArr()

    if (this.userID) {
      this.isLogin = true
      // this.getUserData()
    }

  },
  components:{
    PCHeader,
    PCFooter,
    MyAnswerList,
    PCAskItem,
    PCNewAskList,
    PCCommonNav
  },
  computed:{
    getUserAvatar:function(){
      if (!this.expert) {
        return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
      }
      if(this.expert.avatarPath){
        let indexOf = this.expert.avatarPath.indexOf('http://tu.07358.com')
        if(indexOf != -1){
          return this.expert.avatarPath
        }else {
          return imgBaseUrl + this.expert.avatarPath
        }
      }else {
        return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
      }
    },
    getLabel(){
      if (this.expert.tags) {
        let imags = this.expert.tags.split(',')
        return imags;
      }else {
        return []
      }

    },
    getIsFollow(){
      if (this.isLogin) {
        return this.expert.cangzj.length > 0 ? true : false;
      }else {
        return false
      }
    }
  },
  methods: {
    gotoZJTW(){
      if (this.isLogin) {
        this.$router.push({path: '/publishAsk_pc', query:{zjid:this.expert.id}})
      }else {
        this.$router.push('/login_pc')
      }
    },
    gotoAddress(path,login){
      if (login) {
        if (this.isLogin) {
          this.$router.push(path)
        }else {
          this.$router.push('/login_pc')
        }
      }else {
          this.$router.push(path)
      }
    },
    // 获取专家资料
    getAskArr(){
      let urlStr = ''
      if (this.isLogin) {
        urlStr = '&uid='+this.userID
      }
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/zjdetail?id='+this.eid+urlStr
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          this.expert = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
      });
    },
    // 获取专家问题列表
    getExpert(){
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/zjanswer?id='+this.eid
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          this.askArr = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
      });
    },
    // 获取热门问题
    getNewAsk(){
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/list'
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          this.newAsk = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
      });
    },
    follow(){
      if (!this.validateLogin()) {
        return
      }
      if (this.userID == this.expert.id) {
        Toast('不能关注自己');
        return
      }
      Indicator.open('提交中...');
      let param = {
        'itemid':this.expert.id,
      }
      // let getUrl = this.CFWConfig.APIHost + '/v3/ask/dian?token='+this.userToken
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/gzzj?token='+this.userToken
      this.axios.post(getUrl,this.objToformData(param))
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          this.getAskArr()
        }
        Toast(response.data.message);
        Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    validateLogin(){
      if (!this.isLogin) {
        MessageBox.confirm('请先登录,确定登录吗？').then(({ action }) => {
          this.$router.push('/login_pc')
        }, response => {
        });
        return false
      }else {
        return true
      }
    },
    // 参数转化成表单对象
    objToformData(obj){
      // let params = new URLSearchParams()
      let params = new FormData()
      Object.keys(obj).forEach( (key, i) => {
        params.append(key, obj[key]);
      });
      return params;
    },
    seeing () {
      if (this.searchKey.page === 1) {
        return // 防止偶然性的第一个重复请求的bug
      }
      this.getData()
    },
    isMobile(){
      var sUserAgent = navigator.userAgent;
    	var mobileAgents=['Android','iPhone','Symbian','WindowsPhone','iPod','BlackBerry','Windows CE'];
    	var goUrl = 0;
    	for( var i=0;i<mobileAgents.length;i++){
    			if(sUserAgent.indexOf(mobileAgents[i]) > -1){
    					goUrl = 1;
    					break;
    			}
    	}
      // console.log(goUrl)
    	if (goUrl != 1){
        console.log('pc')
    	}else {
    	  console.log('Mobile')
        this.$router.push('home')
    	}
    }
  },

}
</script>

